import { useState } from 'react';
import { Row, Col, Button, Tooltip } from "antd";
import { Link } from 'react-router-dom';
import delImg from "../../assets/delete.png";
import setImg from "../../assets/set.png";
import materialImg from "../../assets/material.png";
import './index.css';

export default function Keyboard(props) {
    const top = ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"];
    const middle = ["A", "S", "D", "F", "G", "H", "J", "K", "L"]
    const bottom = ["Z", "X", "C", "V", "B", "N", "M"]

    return (
        <div className="kb">
            <div className="input">
                {props.text || "输入搜索商品"}
                <Link to="/set" className="set">
                    <Tooltip title="设置">
                        <img src={setImg} alt="设置" />
                    </Tooltip>
                </Link>
                <Link to="/skus" className="set" style={{ marginLeft: "60px" }}>
                    <Tooltip title="商品管理">
                        <img src={materialImg} alt="商品管理" />
                    </Tooltip>
                </Link>
            </div>
            {top.map((item, index) => <div key={index} onClick={() => props.onCharClick(item)}>{item}</div>)}
            <div style={{ width: "3%", border: "0" }} />
            {middle.map((item, index) => <div key={index} onClick={() => props.onCharClick(item)}>{item}</div>)}
            <div style={{ width: "11%", border: "0" }} />
            {bottom.map((item, index) => <div key={index} onClick={() => props.onCharClick(item)}>{item}</div>)}
            <div className="delete"><img src={delImg} alt="删除" onClick={() => props.onCharClick("del")} /></div>
        </div>
    );
}

