import './index.css';
export default function Counter(props) {
    const counts = [7, 8, 9, 4, 5, 6, 1, 2, 3];
    return (
            <div className="count">
                {counts.map((item, index) => <div key={index} onClick={() => props.onCount(item)} >{item}</div>)}
                <div onClick={() => props.onCount("clear")} style={{ background: "#EB7179", color: "#FFF", fontSize: "35px" }}>清空</div>
                <div onClick={() => props.onCount("0")} style={{ width: "63%" }}>0</div>
        </div>
    );
}

