import { useState } from 'react';
import { Row, Col } from "antd";
import System from "./system";
import Match from "./match";
import menu_cam from "../../assets/camera.png";
import menu_set from "../../assets/setting.png";
import { Link } from 'react-router-dom';
import './index.css';
const Title = e => <div className="title">{e.title}</div>;

export default function Setting() {
    const [menu, setMenu] = useState(0);

    return (
        <Row className="container">
            <Col span={8}>钱江新城|由图智能系统</Col>
            <Col span={8} style={{ textAlign: "center" }}>下午 05：32</Col>
            <Col span={8} style={{ textAlign: "right" }}>称重员  陈达凯</Col>
            <Col span={8}><Link className="back" to="/" replace>{"<"}</Link></Col>
            <Col span={8} style={{ textAlign: "center" }}><h2>{menu === 1 ?"识别设置":"系统设置"}</h2></Col>
            <Col span={24} className="hor_line" />
            <Col span={5} className="side">
                <div onClick={() => setMenu(0)} className={menu === 0 ? "check" : ""}><img src={menu_set} />系统设置</div>
                <div onClick={() => setMenu(1)} className={menu === 1 ? "check" : ""}><img src={menu_cam} />识别设置</div>
            </Col>
            <Col span={19}>
                <div className="content">
                    {menu === 1 ? <Match /> : <System />}
                </div>
            </Col>
        </Row>
    );
}

