import { useState, useEffect } from 'react';
import { Row, Col, Button, Popconfirm, Spin, message } from "antd";
import Keyboard from "../components/Keyboard";
import Counter from "../components/Counter";
import './index.css';
import watchImg from "../../assets/watch.png";
import stepImg1 from "../../assets/step1.png";
import stepImg2 from "../../assets/step2.png";
import emptyImg from "../../assets/empty.png";
import delImg from "../../assets/del.png";
import defaultImg from "../../assets/default.png";
import balanceImg from "../../assets/balance.png";
import { Link, useLocation } from 'react-router-dom';
import *as service from "../../utils/server";
const Radio = (props) => <div className={props.check ? "check" : "uncheck"} onClick={props.onCheck}>{props.children}</div>;
const Empty = () => <>
    <div className='tip1'>
        <img src={stepImg1} />
        <p>第二步：请放上商品后，点击拍照学习。</p>
    </div>
    <div className='tip2'>
        <img src={emptyImg} />
        <p>提示
            1、请拍摄不同数量、不同形态、不同摆放的图片（接近使用时）；
            2、保障拍照的区域包含秤盘且外部区域干扰。
        </p>
    </div>
</>
const EmptySku = () => <div className='emptySku'>
    <img src={balanceImg} style={{ height: "313px" }} alt="空" />
    <p>
        此为测试功能，用于删除识别错的商品，仅供参考；
    </p>
    <p>
        第一步：请放上需要矫正的商品，请尽量采用出错时的摆放方式，然后点击识别。
    </p>
</div>
const Learning = () => <div className='learning'><div className='learningtext'><Spin />学习中，请稍后…</div></div>



export default function Teach(props) {
    const location = useLocation();
    const [skus, setSkus] = useState([]);
    const [input, setInput] = useState("");
    const [menu, setMenu] = useState(0);
    const [isTeach, setType] = useState(location.state.isTeach || false);
    const [learning, setLearning] = useState(false);
    const [plu, setPlu] = useState(location.state.plu || {});
    const [pics, setpics] = useState([]);

    const [checkIndex, setCheckIndex] = useState(1);
    const counts = [7, 8, 9, 4, 5, 6, 1, 2, 3];



    const onInput = e => {
        let str = input || "";
        if (e === "del")
            str = input.slice(0, input.length - 1);
        else if (e === "clear")
            str = "";
        else str = str.length >= 10 ? str : (str + e);
        setInput(str);
    }

    const onDelete = (index) => {
        // 阻止冒泡
        // e.stopPropagation();
        console.log(index)
        pics.splice(index, 1);
        setpics([...pics]);
    }

    const onDeleteStudy = index => {
        const plus = [skus[index].plu_code];
        service.study_delete(plus).then(res => {
            if (res) {
                if (res.code === 200) {
                    message.success("删除成功");
                    skus.splice(index, 1);
                    setSkus([...skus]);
                }
                else if (res.code === 500)
                    message.error(res.msg);
            } else {
                message.error("删除失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    };
    const onSelect = (index) => {
        console.log("ddd", index)
    }
    const onSelectSku = e => {
        setPlu(skus[e]);
        setType(true);
    };
    const SkuItem = ({ item, index, del = false }) => <div className="sku" key={index} onClick={() => onSelectSku(index)}>
        <img src={defaultImg} alt={item.plu_name} />
        <div className="name">{item.plu_name}</div>
        <div className="price">5.26元/kg</div>
        <div className="bottom">
            <div className="code">{item.plu_code}</div>
            <div className="total">¥77.36</div>
        </div>
        {del && <img src={delImg} alt="删除" onClick={() => onDeleteStudy(index)} className='delete' />}
    </div>


    const onStudy = () => {
        service.study_matching().then(res => {
            if (res && res.code === 200) {
                const picsData = [...pics];
                picsData.push(res.data);
                setpics(picsData);
            }

        }).catch(err => { console.error(err) });
    }

    const onSaveStudy = () => {
        if (pics.length === 0) {
            message.error("暂无学习结果");
            return;
        };
        const id_list = pics.map(e => e.id);
        const params = { id_list, plu_code: plu.plu_code };
        service.save_study(params).then(res => {
            if (res) {
                if (res.code === 200) {
                    message.success("已保存学习结果");
                    setpics([]);
                    setType(false);
                    setSkus([]);
                }
                else if (res.code === 500)
                    message.error(res.msg);
            } else {
                message.error("保存失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }

    const onMatch = () => {
        service.ai_matching().then(res => {
            if (res) {
                if (res.code === 200) {
                    const skus = res.data || [];
                    setSkus(skus);
                }
                else if (res.code === 500)
                    message.error(res.msg);
            } else {
                message.error("调用识别失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }
    const onSaveDelete = () => {
        if (skus.length === 0) {
            message.error("无识别记录");
            return;
        }
        const params = skus.map(e => e.plu_code);
        service.correct_study(params).then(res => {
            if (res) {
                if (res.code === 200) {
                    message.success("保存矫正结果成功");
                    setSkus([]);
                }
                else if (res.code === 500)
                    message.error(res.msg);
            } else {
                message.error("保存矫正结果失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }
    const onClearStudy = () => {
        const params = [plu.plu_code];
        service.study_delete(params).then(res => {
            if (res) {
                if (res.code === 200) {
                    message.success("清除历史学习成功");
                }
                else
                    message.error(res.msg || "");
            } else {
                message.error("清除历史学习失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }

    useEffect(() => {
        setCheckIndex(-1);
        service.query_plu(input).then(res => {
            if (res && res.code === 200) { setSkus([res.data] || []) }
            else setSkus([]);
        }).catch(err => { setSkus([]) })

    }, [input])

    useEffect(() => {
        const json = {
            code: 10200,
            data: {
                fps: 25,
                open: 1,
            }
        };
        const ws = new WebSocket(`ws://${window.location.host}/socket`)
        ws.onopen = function () {
            console.log("open");
            let jsonStr = JSON.stringify(json);
            ws.send(jsonStr)
        }
        ws.onclose = function () {
            console.log("close")
        }
        ws.onmessage = e => {
            var data = JSON.parse(e.data);
            if (data.code === 10200) {
                try {
                    let watch = document.getElementById("watch");
                    watch.src = "data:image/jpeg;base64," + data.data;
                } catch (error) {
                }
            } else {
                alert(data.data);
            }
        }
        return () => {
            // let watch = document.getElementById("watch");
            // watch.src = watchImg;
            json.data.open = 0;
            ws.send(JSON.stringify(json))
        };
    }, [])


    return (
        <div className="teach">
            {learning && <Learning />}
            <div className="content" style={{ height: isTeach || menu === 1 ? "100vh" : "" }}>
                <div className='radios'>
                    <Radio check={menu === 0} onCheck={() => setMenu(0)}>商品学习</Radio>
                    <Radio check={menu === 1} onCheck={() => setMenu(1)}>识别矫正（测试版）</Radio>
                </div>
                {
                    (menu === 0 && isTeach) && <div className='picDiv'>
                        {
                            pics.length > 0 && pics.map((item, index) => <div className="pic" key={index} >
                                <img src={`data:image/jpeg;base64,${item.img}`} alt="图片" onClick={() => onSelect(index)} />
                                <img src={delImg} alt="删除" onClick={() => onDelete(index)} />
                            </div>)
                        }
                        {pics.length === 0 && <Empty />}
                    </div>
                }
                {
                    (menu === 1) && <div className='search' style={{ alignItems: "flex-start", paddingTop: "50px" }}>
                        {
                            skus.length > 0 && skus.map((item, index) => <SkuItem item={item} index={index} del={true} />)
                        }
                        {skus.length === 0 && <EmptySku />}
                        {skus.length > 0 && <p style={{ position: "absolute", width: "80%", bottom: 0 }}>第二步：将无关结果删除，然后点击保存</p>}
                    </div>
                }
                {
                    (menu === 0 && !isTeach) &&
                    <div className='search' >
                        {skus.length > 0 ? skus.map((item, index) => <SkuItem item={item} index={index} />) :
                            <div className='step2Empty'>
                                <img src={stepImg2} style={{ height: "30%" }} />
                                <p>第一步：搜索并选择要学习的商品。</p>
                            </div>
                        }
                    </div>
                }
            </div>
            <div className="watch">
                <p>下午 5:32 由图智能系统</p>
                <Link className='quit_btn' to="/" rep> <Button>退出学习模式</Button></Link>
                <div className='camera'>
                    <img src={watchImg} id="watch" />
                </div>
            </div>
            {!isTeach && menu === 0 && <div className="keyboard">
                <Keyboard onCharClick={onInput} text={input} />
            </div>}
            <div className="counterDiv">
                {(menu === 0 && isTeach) &&
                    <>
                        <div className='sku_item'>
                            <div className='code'>{plu.plu_code || ""}</div>
                            <p>名称:<b>{plu.plu_name || ""}</b></p>
                            <p>单价:<b>5.25元/kg</b></p>
                            <p>学习状态:<b>已学习</b></p>
                            <img src={defaultImg} alt="图片" />
                        </div>
                        <Popconfirm
                            title="删除后就将无法恢复此次学习记录"
                            okText="确定"
                            cancelText="取消"
                            onConfirm={onClearStudy}
                        >
                            <Button className='clear_btn'>清除历史学习</Button>
                        </Popconfirm>
                        <Popconfirm
                            title="确认后将覆盖历史数据，请确保现在的学习与实际操作保持一致！"
                            okText="确定"
                            cancelText="取消"
                            onConfirm={onSaveStudy}
                        >
                            <Button className='save_btn'>保存学习结果</Button>
                        </Popconfirm>
                        <Button className='match_btn' onClick={onStudy}>拍照学习</Button>
                    </>
                }
                {(menu === 0 && !isTeach) && <Counter onCount={onInput} />}
                {(menu === 1) && <>
                    <Button className='match_btn' style={{ marginTop: "5%" }} onClick={onMatch}>点击识别</Button>
                    <Button className='save_btn' style={{ marginBottom: "40%" }} onClick={onSaveDelete}>保存矫正结果</Button>
                </>}
            </div>
        </div>
    );
}

