import axios from 'axios';
import { notification } from 'antd';
import moment from 'moment';

// export const baseAuthURL = `${configs[process.env.API_ENV].API_SERVER}`;
export const baseAuthURL = "/api/v3";




export default async function request(url, options) {
    const baseURL = baseAuthURL;

    let showNotify = true;
    const opts = {
        baseURL,
        url,
        validateStatus() {
            return true;
        },
        ...options,
    };
    if (opts.notNotify) {
        showNotify = false;
    }

    const defaultHeader = {
        // Authorization: await getAccessToken(),
    };
    if (opts.method === 'POST' || opts.method === 'PUT'|| opts.method === 'DELETE') {
        defaultHeader['Content-Type'] = 'application/json; charset=utf-8';
        opts.data = opts.body;
    }

    opts.headers = { ...defaultHeader, ...opts.headers };
    if (opts.responseType === 'blob') {
        return axios.request(opts).then(response => {
            const { status, data } = response;

            if (status >= 200 && status < 300) {
                let fileName = opts.defaultFileName;
                if (response.headers['content-disposition']) {
                    fileName = decodeURI(response.headers['content-disposition'].split('=')[1]);
                }
                let elink = document.createElement('a');
                elink.download = fileName;
                elink.href = window.URL.createObjectURL(response.data);
                elink.click();
                window.URL.revokeObjectURL(elink.href); // 释放URL 对象
                elink = null;
                return data;
            }

            const reader = new FileReader(); // 创建读取文件对象
            reader.onload = e => {
                const json = JSON.parse(e.target.result); // 返回的数据
                // if (status === 401) {
                //     const {
                //         error: { code },
                //     } = json;
                //     if (code === 9999) {
                //         // 清空访问令牌
                //         store.clearAccessToken();
                //         /* eslint-disable no-underscore-dangle */
                //         window.g_app._store.dispatch({ type: 'login/logout' });
                //         return {};
                //     }
                // }
                const error = {
                    code: 0,
                    message: '服务器发生错误',
                };

                if (status === 504) {
                    error.message = '未连接到服务器';
                } else if (json) {
                    const {
                        error: { message, code },
                    } = json;
                    error.message = message;
                    error.code = code;
                } else if (status >= 400 && status < 500) {
                    error.message = '请求发生错误';
                }
                if (showNotify) {
                    notification.error({
                        message: `ERROR\n${opts.baseURL}${opts.url}`,
                        description: error.message,
                    });
                }
                return { error, status };
            };
            reader.readAsText(data, 'utf-8'); // 设置读取的数据以及返回的数据类型为utf-8
            return {};
        });
    }

    return axios.request(opts).then(response => {
        const { status, data } = response;

        if (status >= 200 && status < 300) {
            return data;
        }
        if (status === 401) {
            const {
                error: { code },
            } = data;
            // if (code === 9999) {
            //     // 清空访问令牌
            //     store.clearAccessToken();
            //     /* eslint-disable no-underscore-dangle */
            //     window.g_app._store.dispatch({ type: 'login/logout' });
            //     return {};
            // }
        }

        const error = {
            code: 0,
            message: '服务器发生错误',
        };
        if (status === 504) {
            error.message = '未连接到服务器';
        } else if (data) {
            const {
                error: { message, code },
            } = data;
            error.message = message;
            error.code = code;
        } else if (status >= 400 && status <= 500) {
            error.message = '请求发生错误';
        }
        if (showNotify) {
            notification.error({
                message: `ERROR\n${opts.baseURL}${opts.url}`,
                description: error.message,
            });
        }
        return { error, status };
    });
}
