import { stringify } from 'qs';
import request from '../utils/request';

/**
 * 查询商品信息
 */
export async function query_plu(plu) {
  return request(`/plu?plu_code=${plu}`);
}

/**
 * 获取相机裁剪区域信息
 */
export async function get_crop(plu) {
  return request(`/camera/area`);
}


/**
 * 配置相机裁剪区域信息 复制接口
 */
export async function set_crop(params) {
  return request(`/camera/area`, {
    method: "PUT",
    body: params
  });
}



/**
 * AI识别
 */
export async function ai_matching() {
  const params = { max_num: 5, weight: 100, forcibly: 1, stable: 1 }
  return request(`/ai/matching`, {
    method: "POST",
    body: params
  });
}
/**
 * AI识别
 */
export async function ai_mark(params) {
   const body = {
    ...params,
    weight: 160, //重量
    price: 166, //价格
    device_no: "01", //秤号
    bar_code: "14789563485797892716", //条形码
    operator_name: "操作员1" //操作员名称
  }
  return request(`/ai/mark`, {
    method: "POST",
    body
  });
}
/**
 * 学习模式--识别
 */
export async function study_matching(plu) {
  return request(`/study/matching`);
}


/**
 * 学习模式--保存
 */
export async function save_study(params) {
  const others = {
    mark_type: 9, //（1-"自动打印"，2-"点选"，3-"快捷键"，4-"搜索"，9-”学习模式“）
    weight: 160, //重量
    price: 166, //价格
    device_no: "01", //秤号
    bar_code: "14789563485797892716", //条码
    operator_name: "操作员1" //操作员名称
  }

  return request(`/study/mark`, {
    method: "POST",
    body: { ...params, ...others }
  });
}


/**
 * 删除商品学习数据
 */
export async function study_delete(params) {
  return request(`/study/data`, {
    method: "DELETE",
    body: params
  });
}


/**
 * 矫正商品学习数据
 */
 export async function correct_study(params) {
  return request(`/study/correct`, {
    method: "POST",
    body: params
  });
}



/**
 * 同步学习数据到本机
 */
export async function sync_study(params) {
  return request(`/study/data`, {
    method: "PUT",
    body: params
  });
}

/**
 * 修改商品状态
 */
export async function plu_status(params) {
  return request(`/plu/status`, {
    method: "PUT",
    body: params
  });
}

/**
 * 获取设备信息
 */
 export async function device_info() {
  return request(`/device/info`);
}