import { Button, message } from 'antd';
import { useEffect, useRef, useState } from 'react';
import * as service from "../../../utils/server";
import './index.css';

export default function System() {
    const canvasRef = useRef();
    var canvas, ctx;
    const dw = 12;
    let left_top_x = 10;
    let left_top_y = 10;
    let right_top_x = 110;
    let right_top_y = 10;
    let left_bottom_x = 10
    let left_bottom_y = 110
    let right_bottom_x = 110
    let right_bottom_y = 110
    var isMoving = false;
    //0:未选中 1：in 2:右下角
    var position = 0;
    var lastX = 0;
    var lastY = 0;
    var ratio = 0.2;
    var total_width = 0;
    var total_height = 0;
    const minSize = 100;
    var image = new Image();


    const drawArc = (x, y) => {
        ctx.moveTo(x, y);
        ctx.arc(x, y, dw, 0, Math.PI * 2, false);
    }

    const drawCrop = () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(image, 0, 0);
        ctx.fillStyle = "rgba(0, 0, 0, 0.8)";

        ctx.moveTo(0, 0);
        ctx.lineTo(left_top_x, left_top_y);
        ctx.lineTo(left_bottom_x, left_bottom_y);
        ctx.lineTo(right_bottom_x, right_bottom_y);
        ctx.lineTo(right_top_x, right_top_y);
        ctx.lineTo(left_top_x, left_top_y);
        ctx.lineTo(0, 0);
        ctx.lineTo(canvas.width, 0);
        ctx.lineTo(canvas.width, canvas.height);
        ctx.lineTo(0, canvas.height);
        ctx.lineTo(0, 0);
        ctx.fill();


        ctx.strokeStyle = "#FFF";
        ctx.fillStyle = "#F40";

        ctx.beginPath();
        ctx.moveTo(left_top_x, left_top_y);
        ctx.lineTo(left_bottom_x, left_bottom_y);
        ctx.lineTo(right_bottom_x, right_bottom_y);
        ctx.lineTo(right_top_x, right_top_y);
        ctx.lineTo(left_top_x, left_top_y);
        ctx.stroke();
        ctx.beginPath();

        drawArc(left_top_x, left_top_y)
        drawArc(right_bottom_x, right_bottom_y)
        drawArc(right_top_x, right_top_y)
        drawArc(left_bottom_x, left_bottom_y)
        ctx.closePath();

        ctx.fill();
    }

    // 鼠标选中画布位置
    function onMouseDown(e) {
        const { offsetX, offsetY } = e.nativeEvent;
        lastX = offsetX;
        lastY = offsetY;
        // if (offsetX > x + dw && offsetX < x - dw + width && offsetY > y + dw && offsetY < y - dw + height) {
        //     position = 1;
        // } else if (offsetX > x - dw / 2 + width && offsetX < x + dw / 2 + width && offsetY > y - dw / 2 + height && offsetY < y + dw / 2 + height) {
        //     position = 2;
        // } else {
        //     position = 0;
        // }

        if (offsetX > left_top_x - dw && offsetX < left_top_x + dw && offsetY > left_top_y - dw && offsetY < left_top_y + dw) {
            position = 1;
        } else if (offsetX > right_top_x - dw && offsetX < right_top_x + dw && offsetY > right_top_y - dw && offsetY < right_top_y + dw) {
            position = 2;
        } else if (offsetX > right_bottom_x - dw && offsetX < right_bottom_x + dw && offsetY > right_bottom_y - dw && offsetY < right_bottom_y + dw) {
            position = 3;
        } else if (offsetX > left_bottom_x - dw && offsetX < left_bottom_x + dw && offsetY > left_bottom_y - dw && offsetY < left_bottom_y + dw) {
            position = 4;
        } else {
            position = 0;
        }
        isMoving = true;
    }

    const onMouseMove = e => {
        if (!isMoving || position === 0) return;
        const { offsetX, offsetY } = e.nativeEvent;
        var dx = offsetX - lastX;
        var dy = offsetY - lastY;


        if (left_top_x < 0) {
            left_top_x = 0;
        }
        if (left_bottom_x < 0) {
            left_bottom_x = 0;
        }


        if (left_top_y < 0) {
            left_top_y = 0;
        }
        if (right_top_y < 0) {
            right_top_y = 0;
        }


        if (right_top_x > canvas.width) {
            right_top_x = canvas.width;
        }

        if (right_bottom_x > canvas.width) {
            right_bottom_x = canvas.width;
        }

        if (left_bottom_y > canvas.height) {
            left_bottom_y = canvas.height;
        }

        if (right_bottom_y > canvas.height) {
            right_bottom_y = canvas.height;
        }

        switch (position) {
            case 1:
                left_top_x += dx;
                left_top_y += dy;
                break;
            case 2:
                right_top_x += dx;
                right_top_y += dy;
                break;
            case 3:
                right_bottom_x += dx;
                right_bottom_y += dy;
                break;
            case 4:
                left_bottom_x += dx;
                left_bottom_y += dy;
                break;
            default: break;
        }
        if (Math.min(right_top_x, right_bottom_x) - Math.max(left_top_x, left_bottom_x) < ratio * minSize || Math.min(left_bottom_y, right_bottom_y) - Math.max(left_top_y, right_top_y) < ratio * minSize) {
            switch (position) {
                case 1:
                    left_top_x -= dx;
                    left_top_y -= dy;
                    break;
                case 2:
                    right_top_x -= dx;
                    right_top_y -= dy;
                    break;
                case 3:
                    right_bottom_x -= dx;
                    right_bottom_y -= dy;
                    break;
                case 4:
                    left_bottom_x -= dx;
                    left_bottom_y -= dy;
                    break;
                default: break;
            }
            return;
        }

        // if (position === 1) {
        //     x = x + dx;
        //     y = y + dy;
        // } else if (position === 2) {
        //     var d;
        //     if (Math.abs((dx)) >= Math.abs(dy)) d = dx;
        //     else d = dy;
        //     height += d;
        //     width += d;
        // }
        drawCrop();
        lastX = offsetX;
        lastY = offsetY;

        // document.getElementsByTagName("p")[0].style.display = "block";
    }

    const onMouseLeave = () => { isMoving = false; }
    const onMouseUp = () => { isMoving = false; }

    // axios.get("http://127.0.0.1:18000/api/v3/system/img/crop", {
    //     headers: {
    //         "Sign": key,
    //         "Timestamp": Math.round((new Date()).getTime() / 1000)
    //     }
    // })
    //     .then(function (response) {
    //         console.log(response);
    //         const { data } = response.data;
    //         total_width = data.total_width;
    //         total_height = data.total_height;

    //         canvas = document.getElementsByTagName("canvas")[0];
    //         ctx = canvas.getContext("2d");
    //         if (canvas.width === 0 || canvas.height === 0) {
    //             canvas.width = window.innerWidth * 0.7;
    //             canvas.height = total_height * window.innerWidth * 0.7 / total_width;
    //             var img = document.getElementById("bg");
    //             img.width = canvas.width;
    //             img.height = canvas.height;
    //             ratio = canvas.width / total_width;
    //             x = data.left_x * ratio;
    //             y = data.left_y * ratio;
    //             width = data.width * ratio;
    //             height = data.height * ratio;
    //             drawCrop();
    //         }
    //     }, function (err) {
    //         console.log(err);
    //         alert(err);
    //     })


    const resizeCrop = (data) => {
        total_width = data.total_width || 0;
        total_height = data.total_height || 0;

        if (canvas.width === 0 || canvas.height === 0) {
            canvas.width = window.innerWidth * 0.6;
            canvas.height = total_height * window.innerWidth * 0.6 / total_width;
            ratio = canvas.width / total_width;
            left_bottom_x = data.left_bottom_x * ratio;
            left_bottom_y = data.left_bottom_y * ratio;
            left_top_x = data.left_top_x * ratio;
            left_top_y = data.left_top_y * ratio;
            right_bottom_x = data.right_bottom_x * ratio;
            right_bottom_y = data.right_bottom_y * ratio;
            right_top_x = data.right_top_x * ratio;
            right_top_y = data.right_top_y * ratio;
            drawCrop();
        }
    }

    useEffect(() => {
        canvas = canvasRef.current;
        ctx = canvas.getContext("2d");
        drawCrop();

        service.get_crop().then(res => {
            if (res && res.code === 200) {
                resizeCrop(res.data)
            }
        }).catch(err => {
            console.error(err);
        })

        const json = {
            code: 10200,
            data: {
                fps: 25,
                open: 1,
            }
        };
        const ws = new WebSocket(`ws://${window.location.host}/socket`)
        ws.onopen = function () {
            console.log("open");
            let jsonStr = JSON.stringify(json);
            ws.send(jsonStr)
        }
        ws.onclose = function () {
            console.log("close")
        }
        ws.onmessage = e => {
            var data = JSON.parse(e.data);
            if (data.code === 10200) {
                image.src = "data:image/jpeg;base64," + data.data;
                window.requestAnimationFrame(drawCrop);
            } else {
                alert(data.data);
            }
        }
        return () => {
            json.data.open=0;
            ws.send(JSON.stringify(json))
        };
    }, [])


    const saveCropInfo = () => {
        const params = {
            left_top_x: parseInt(left_bottom_x / ratio),
            left_top_y: parseInt(left_top_y / ratio),
            left_bottom_x: parseInt(left_bottom_x / ratio),
            left_bottom_y: parseInt(left_bottom_y / ratio),
            right_top_x: parseInt(right_top_x / ratio),
            right_top_y: parseInt(right_top_y / ratio),
            right_bottom_x: parseInt(right_bottom_x / ratio),
            right_bottom_y: parseInt(right_bottom_y / ratio),
        }
        service.set_crop(params).then(function (response) {
            if (response && response.code === 200) {
                message.success("设置成功");
            } else {
                if (response && response.msg) {
                    message.error(response.msg);;
                } else {
                    message.error("设置失败");;
                }
            }
        }).catch(err => {
            console.error(err);
            message.error(err);
        })
    };
    return (
        <div style={{ textAlign: "center" }}>
            <canvas
                ref={canvasRef}
                width={0}
                height={0}
                onMouseDown={onMouseDown}
                onMouseUp={onMouseUp}
                onMouseMove={onMouseMove}
                onMouseLeave={onMouseLeave}
            >
            </canvas>
            {/* <img
                id="bg"
                // src={"https://img0.baidu.com/it/u=13255978,3987556015&fm=26&fmt=auto"}
                alt="摄像头"
                style={{ width: "500px", height: "500px" }}
            /> */}
            <Button type='primary' style={{ width: "300px", marginTop: "50px" }} onClick={saveCropInfo}>保存</Button>
        </div>
    );
}

