import { useEffect, useState } from 'react';
import { Row, Col, Button, Empty, message } from "antd";
import Keyboard from "../components/Keyboard";
import Counter from '../components/Counter';
import './index.css';
import { Link, useNavigate } from 'react-router-dom';
import defaultImg from "../../assets/default.png";
import *as service from "../../utils/server";
export default function Setting() {
    const navigate = useNavigate();
    const [skus, setSkus] = useState([]);
    const [input, setInput] = useState("");
    const [checkIndex, setCheckIndex] = useState(-1);
    const counts = [7, 8, 9, 4, 5, 6, 1, 2, 3];


    const onInput = e => {
        let str = input || "";
        if (e === "del")
            str = input.slice(0, input.length - 1);
        else if (e === "clear")
            str = "";
        else str = str.length >= 10 ? str : (str + e);
        setInput(str);
    }


    const toTeach = (b) => {
        const params = { isTeach: b };
        if (b) {
            params.plu = {
                plu_code: skus[checkIndex].plu_code,
                plu_name: skus[checkIndex].plu_name
            }
        }
        navigate("/teach", { state: { ...params } })
    }

    const onMatch = () => {
        setInput("");
        setCheckIndex(-1);
        service.ai_matching().then(res => {
            if (res) {
                if (res.code === 200)
                    setSkus(res.data || []);
                else if (res.code === 500)
                    message.error(res.msg);
            } else {
                message.error("调用识别失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }


    const onSelectSku = index => {
        setCheckIndex(index);
        if (index >= 0) {
            const params = { plu_code: skus[index].plu_code, mark_type: 2 };
            if (input && input.length > 0) {
                params.mark_type = 4;
            }
            service.ai_mark(params).then(res => {
                if (res) {
                    if (res.code === 200)
                        message.success("标记成功");
                    else
                        message.error(res.msg || "");
                } else {
                    message.error("调用标记失败");
                }
            }).catch(err => { console.error(err); message.error(err); });
        }
    }
    useEffect(() => {
        setCheckIndex(-1);
        if (!input) return;
        service.query_plu(input).then(res => {
            if (res && res.code === 200) { setSkus([res.data] || []) }
            else setSkus([]);
        }).catch(err => { setSkus([]) })
    }, [input])


    return (
        <div className="opt">
            <div className="leftpanel">
                <div className="title">识别结果</div>
                <div className="skus">
                    {
                        (skus.length > 0 && checkIndex === -1) && skus.map((item, index) => <div className="sku" key={index} onClick={() => onSelectSku(index)}>
                            <img src={defaultImg} alt="图片" />
                            <div className="name">{item.plu_name}</div>
                            <div className="price">5.26元/kg</div>
                            <div className="bottom">
                                <div className="code">{item.plu_code}</div>
                                <div className="total"><p>合计</p>¥77.36</div>
                            </div>
                        </div>)
                    }{
                        skus.length === 0 && <Empty description="未识别到商品" style={{ marginTop: "10%" }} />
                    }{
                        (skus.length > 0 && checkIndex >= 0) && <div className="check">
                            <div>
                                <b style={{ color: "#37C5B5" }}>编码:{skus[checkIndex].plu_code}</b>
                                <b style={{ color: "#0B0B0B" }}>名称:{skus[checkIndex].plu_name}</b>
                                <b style={{ color: "#E34550" }}>单价:5.26元/kg</b>
                            </div>
                            <img src={defaultImg} alt="图片" />
                            <Button onClick={() => toTeach(true)}>进入学习</Button>
                        </div>
                    }
                </div>
                <div className="keyboard">
                    <Keyboard className="keyboard" onCharClick={onInput} text={input} />
                </div>
            </div>
            <div className="rightpanel">
                <p>下午 5:32 由图智能系统</p>
                <div className="item">结算价格<b>--</b><span>元</span></div>
                <div className="item">单价<b>--</b><span>元/kg</span></div>
                <div className="item">重量<b>--</b><span>kg</span></div>
                <Button type="primary" className="btn1" onClick={() => toTeach(false)}>学习模式</Button>
                {/* <Button type="primary" className="btn1">识别管理</Button> */}
                {/* <Button type="primary" className="btn2">点击保存学习</Button> */}
                <Button type="primary" className="btn2" onClick={onMatch}>点击识别</Button>
                <div className="counter">
                    <Counter onCount={onInput} />
                    {/* {counts.map((item, index) => <div key={index} onClick={() => onInput(item)} >{item}</div>)}
                    <div onClick={() => onInput("clear")} style={{ background: "#EB7179", color: "#FFF", fontSize: "35px" }}>清空</div>
                    <div onClick={() => onInput("0")} style={{ width: "63%" }}>0</div> */}
                </div>
            </div>
        </div>
    );
}

