import { useState, useEffect } from 'react';
import { Button, Input, Table, Badge, message } from "antd";
import *as service from "../../utils/server";
import './index.css';
import { useNavigate } from 'react-router';

const { Search } = Input;
export default function Skus(props) {
    const navigate = useNavigate();
    const [input, setInput] = useState("");
    const [skus, setSkus] = useState([]);
    const columns = [{
        title: "商品名称",
        dataIndex: "plu_name",
        key: "plu_name",
    }, {
        title: "商品码",
        dataIndex: "plu_code",
        key: "plu_code",
    }, {
        title: "上下架状态",
        dataIndex: "is_on",
        key: "is_on",
        render: text => text ? <Badge status='success' text="上架" /> : <Badge status='error' text="下架" />
    }, {
        title: "锁定状态",
        dataIndex: "is_lock",
        key: "is_lock",
        render: text => text ? <Badge status='error' text="锁定" /> : <Badge status='success' text="未锁定" />
    }, {
        title: "操作",
        dataIndex: "operation",
        key: "operation",
        align: "center",
        render: () => <Button className='btn' onClick={toTeach}>进入学习</Button>
    }]

    const toTeach = () => {
        const params = {
            isTeach: true,
            plu: {
                plu_code: skus[0].plu_code,
                plu_name: skus[0].plu_name
            }
        }
        navigate("/teach", { state: { ...params } })
    }
    const onStatusChange = (type, b) => {
        const params = { ...skus[0],key:0 }
        if (type === 0) {
            params.is_lock = b ? 1 : 0;
        } else {
            params.is_on = b ? 1 : 0;
        }
        service.plu_status(params).then(res => {
            if (res) {
                if (res.code === 200) {
                    message.success("修改商品状态成功")
                    setSkus([params]);
                } else
                    message.error(res.msg || "");
            } else {
                message.error("修改商品状态失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }

    useEffect(() => {
        if (!input) return;
        service.query_plu(input).then(res => {
            if (res) {
                if (res.code === 200)
                    setSkus([res.data || {}]);
                else
                    message.error(res.msg || "");
            } else {
                message.error("商品信息获取失败");
            }
        }).catch(err => { console.error(err); message.error(err); });
    }, [input])
    return (
        <div className="skus">
            <div className='menus'>
                <Search className='input' placeholder='请输入商品码' onSearch={setInput} allowClear />
                {skus.length > 0 &&
                    <span>
                        {skus[0].is_lock === 0 && <Button type='danger' onClick={() => onStatusChange(0, true)}>锁定</Button>}
                        {skus[0].is_lock === 1 && <Button className='btn' onClick={() => onStatusChange(0, false)}>解锁</Button>}
                        {skus[0].is_on === 1 && <Button type='danger' onClick={() => onStatusChange(1, false)}>下架</Button>}
                        {skus[0].is_on === 0 && <Button className='btn' onClick={() => onStatusChange(1, true)}>上架</Button>}
                    </span>
                }
            </div>
            <Table
                columns={columns}
                dataSource={skus}
                className='table'
            />
        </div>
    );
}

